@import '../../../../../styles/templarios.abstracts';

.product-detail {
  display: flex;
  gap: 32px;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;

  @include breakpoint-down('md') {
    flex-direction: column;
    gap: 24px;
  }

  &__left {
    flex: 1;
    align-self: center;
    overflow: hidden;
  }

  &__right {
    flex: 1;
  }

  &__image-container {
    display: flex;
    overflow: hidden;

    @include breakpoint-down('md') {
      width: 50%;
      margin: 0 auto;
    }

    @include breakpoint-down('sm') {
      width: 100%;
      margin: 0;
    }
  }

  &__price-container {
    margin-bottom: 16px;
  }

  &__description {
    display: block;
    margin-bottom: 16px;
  }

  &__info-container {
    margin-bottom: 24px;
  }

  &__plusminus {
    justify-content: flex-start !important;
    margin-bottom: 16px;
  }

  &__message {
    margin-top: 8px;
    display: block;
  }
}

::slotted([slot='image']) {
  border-radius: 8px;
}

::slotted([slot='title']) {
  display: block;
  margin-bottom: 24px;
}

::slotted([slot='regular-price']) {
  margin-right: 8px;
  text-decoration: line-through;
}

::slotted([slot='description']) {
  display: block;
  margin-bottom: 16px;
}

::slotted([slot='attributes']) {
  display: flex;
  flex-direction: column;
}

::slotted([slot='categories']) {
  display: flex;
  align-items: center;
}
