import {
  Component,
  Event,
  EventEmitter,
  Host,
  Listen,
  Prop,
  State,
  h,
} from '@stencil/core';
import { AddToCartData } from '../../feature/product-info/utils/product-info.interface';
import { ProductDetail } from './utils/product-detail.interface';

@Component({
  tag: 'tp-elem-product-detail',
  styleUrl: 'product-detail.scss',
  scoped: true,
})
export class TpElemProductDetail {
  /**
   * Dados do produto
   */
  @Prop({ reflect: true }) productData: ProductDetail;

  /**
   * Quantidade do produto a ser inserido no carrinho, default Ã© 1.
   */
  @State() value: number = 1;

  /**
   * Emite o novo cartCount.
   */
  @Event() addToCartEvent: EventEmitter<AddToCartData>;

  async onAddToCart(productId: number, quantity: number) {
    this.addToCartEvent.emit({
      productId,
      quantity,
    });
  }

  @Listen('tpChangeAlt', { target: 'window' })
  handlePlusminus(event: CustomEvent<number>) {
    this.value = event.detail;
  }

  render() {
    const { productData, value } = this;
    const { id, stockQuantity, stockStatus, type } = productData;

    return (
      <Host>
        <div class="product-detail">
          <div class="product-detail__left">
            <div class="product-detail__image-container">
              <slot name="image" />
            </div>
          </div>

          <div class="product-detail__right">
            <slot name="title" />

            <div class="product-detail__price-container">
              <slot name="regular-price" />
              <slot name="price" />
            </div>

            <slot name="description" />
            <slot name="stock" />

            <div class="product-detail__info-container">
              <slot name="sku" />
              <slot name="attributes" />
              <slot name="categories" />
            </div>

            {type === 'external' ? (
              <slot name="action" />
            ) : (
              <div>
                <tp-plusminus
                  class="product-detail__plusminus"
                  use-slot="false"
                  value={value}
                  min={1}
                  max={stockQuantity ? stockQuantity : 9}
                  automatic-disabled
                ></tp-plusminus>
                <ion-button
                  color="brand"
                  onClick={() => this.onAddToCart(id, value)}
                  disabled={stockStatus !== 'instock'}
                >
                  Add to cart
                </ion-button>
              </div>
            )}
          </div>
        </div>
      </Host>
    );
  }
}
